export function exportLayout(graph) {
    var pos = new Float64Array(graph.order * 2);
    var i = 0;
    graph.forEachNode(function (node, attr) {
        pos[i++] = attr.x;
        pos[i++] = attr.y;
    });
    return pos;
}
export function applyLayout(graph, layout) {
    if (layout.length !== graph.order * 2)
        throw new Error('nansi/lib/utils/applyLayout: layout length is not consistent with graph order!');
    var i = 0;
    graph.updateEachNodeAttributes(function (node, attr) {
        attr.x = layout[i++];
        attr.y = layout[i++];
        return attr;
    }, { attributes: ['x', 'y'] });
}
export function isNumber(value) {
    return typeof value === 'number' && !isNaN(value);
}
